"use client"

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { MapPin, CloudRain, Thermometer, Droplets } from "lucide-react"
import { useEffect, useState } from "react"



const API_KEY = "76020ba6211341f98f960025252111"

type Farm = {
  id: number
  name: string
  location: string
  area: string
  lat: number
  lng: number
  status: string
  currentCrop: string
  stage: string
  image: string
  weather?: {
    temp: number
    humidity: number
    rainfall: number
  }
}

export function FarmOverview() {
 const [farms, setFarms] = useState<Farm[]>([
    {
      id: 1,
      name: "Zone 1",
      location: "Chapu",
      area: "1370 acres",
      lat:29.0970993,
      lng:72.23509979,
      status: "Active",
      weather: { temp: 32, humidity: 45, rainfall: 5 },
      currentCrop: "Wheat",
      stage: "Growth & Tillering",
      image: "/green-farm-field.jpg",
    },
    {
      id: 2,
      name: "Zone 2",
      location: "Moujgarh",
      lat:29.15530014,
      lng:72.12719727,
      area: "4415 acres",
      status: "Active",
      weather: { temp: 30, humidity: 42, rainfall: 3 },
      currentCrop: "Wheat",
      stage: "Pre-Sowing",
      image: "/agricultural-field-maize.jpg",
    },
      {
      id: 3,
      name: "Zone 3",
      location: "Moujgarh",
      lat: 29.13689995,
      lng: 72.1135025,
      area: "3074 acres",
      status: "Active",
      weather: { temp: 30, humidity: 42, rainfall: 3 },
      currentCrop: "Wheat",
      stage: "Pre-Sowing",
      image: "/agricultural-field-maize.jpg",
    },
      {
      id: 4,
      name: "Zone 4",
      location: "Moujgarh",
      area: "3339 acres",
      lat: 29.0984001,
      lng: 72.05619812,
      status: "Active",
      weather: { temp: 30, humidity: 42, rainfall: 3 },
      currentCrop: "Wheat / Raya",
      stage: "Pre-Sowing",
      image: "/agricultural-field-maize.jpg",
    },
      {
      id: 5,
      name: "Zone 5",
      location: "Moujgarh",
      area: "5622 acres",
      lat: 29.07119942,
      lng: 72.0970993,
      status: "Active",
      weather: { temp: 30, humidity: 42, rainfall: 3 },
      currentCrop: "Maize",
      stage: "Pre-Sowing",
      image: "/agricultural-field-maize.jpg",
    },
      {
      id: 6,
      name: "Zone 6",
      location: "Moujgarh",
      area: "5622 acres",
      lat: 29.08320045,
      lng: 72.13790131,
      status: "Active",
      weather: { temp: 30, humidity: 42, rainfall: 3 },
      currentCrop: "Wheat",
      stage: "Pre-Sowing",
      image: "/agricultural-field-maize.jpg",
    },
  ])

 useEffect(() => {
    const fetchWeather = async () => {
      const today = new Date().toISOString().split("T")[0] // YYYY-MM-DD

      const updatedFarms = await Promise.all(
        farms.map(async (farm) => {
          try {
            const url = `https://api.weatherapi.com/v1/history.json?key=${API_KEY}&q=${farm.lat},${farm.lng}&dt=${today}`

            const res = await fetch(url)
            const data = await res.json()

            const day = data?.forecast?.forecastday?.[0]?.day

            return {
              ...farm,
              weather: {
                temp: day?.avgtemp_c ?? 0,
                humidity: day?.avghumidity ?? 0,
                rainfall: day?.totalprecip_mm ?? 0,
              },
            }
          } catch (error) {
            console.error("Weather fetch failed:", error)
            return farm
          }
        })
      )

      setFarms(updatedFarms)
    }

    fetchWeather()
  }, [])

  return (
    <Card>
      <CardHeader>
        <CardTitle>Farm Overview</CardTitle>
        <CardDescription>
          Monitor your farms and current conditions
        </CardDescription>
      </CardHeader>

      <CardContent className="space-y-4">
        {farms.map((farm) => (
          <div
            key={farm.id}
            className="border border-border rounded-lg overflow-hidden hover:shadow-lg transition-shadow"
          >
            <div className="h-32 bg-muted overflow-hidden">
              <img
                src={farm.image}
                alt={farm.name}
                className="w-full h-full object-cover"
              />
            </div>

            <div className="p-4 space-y-3">
              <div className="flex justify-between">
                <div>
                  <h3 className="font-semibold text-lg">{farm.name}</h3>
                  <div className="flex items-center gap-2 text-sm text-muted-foreground">
                    <MapPin size={16} />
                    {farm.location}
                  </div>
                </div>

                <span className="px-3 py-1 bg-primary/10 text-primary text-xs font-semibold rounded-full">
                  {farm.status}
                </span>
              </div>

              <div className="grid grid-cols-2 gap-2 text-sm">
                <div>
                  <p className="text-muted-foreground">Area</p>
                  <p className="font-semibold">{farm.area}</p>
                </div>
                <div>
                  <p className="text-muted-foreground">Current Crop</p>
                  <p className="font-semibold">{farm.currentCrop}</p>
                </div>
              </div>

              {farm.weather && (
                <div className="bg-muted/50 rounded-lg p-3">
                  <p className="text-xs font-semibold uppercase text-muted-foreground">
                    Stage: {farm.stage}
                  </p>

                  <div className="grid grid-cols-3 gap-2 text-center text-xs mt-2">
                    <div>
                      <Thermometer className="mx-auto mb-1" size={16} />
                      <p className="font-semibold">
                        {farm.weather.temp}°C
                      </p>
                    </div>
                    <div>
                      <Droplets className="mx-auto mb-1" size={16} />
                      <p className="font-semibold">
                        {farm.weather.humidity}%
                      </p>
                    </div>
                    <div>
                      <CloudRain className="mx-auto mb-1" size={16} />
                      <p className="font-semibold">
                        {farm.weather.rainfall}mm
                      </p>
                    </div>
                  </div>
                </div>
              )}

              <Button className="w-full">View Details</Button>
            </div>
          </div>
        ))}
      </CardContent>
    </Card>
  )
}